import { withNamespaces } from 'react-i18next';

import i18n from '../i18n';
import az900 from "../Assets/Img/az900.png"

const Certifications = () => {    

    return (
        <div className="mt-5 Certifications">
            <h1> {i18n.t('Certifications')}</h1>
            <img className="w-25" src={az900} alt="az-900 certification"/>
            <div>
            Microsoft Certified: Azure Fundamentals<br/>

            {i18n.t('AZ900Issued')}
            {i18n.t('AZ900IssuedDate')}

            </div>
        </div>
    )
}
export default withNamespaces()(Certifications);