import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";

import i18n from '../i18n';
import { Element } from 'react-scroll'
import { withNamespaces } from 'react-i18next';
import "../CSS/Project.css"

const Project= () => {    
    const projectList =[{title:"Portfolio", imageUrl:require('../Assets/Img/portfolio.png'), projectUrl:"https://github.com/MonicaYam/portfolio"},
                        {title:"Weather Application", imageUrl:require('../Assets/Img/happybidding.png'), projectUrl:"https://github.com/MonicaYam/Vue.js_weather-app"},
                        {title:"Happy Bidding", imageUrl:require('../Assets/Img/weatherapp.png'), projectUrl:""},
                        {title:"Calculator", imageUrl:require('../Assets/Img/calculator.png'), projectUrl:"https://github.com/MonicaYam/QT_Calculator"}]

    return (
        <Element name="projectSection"  className="Project">
        <Container className="text-center py-4">
            <h1>My Recent Project</h1>
            <Row className="pt-5">
                {projectList.map((project, index) => {
                    return(
                        <Col md={{span:4}} key={index}>
                        <Card style={{ width: '18rem' }}>
                            <Card.Img variant="top" src={project.imageUrl} />
                            <Card.Body>
                                <Card.Title>{project.title}</Card.Title>
                                <Card.Text>
                                    {i18n.t(project.title)}
                                </Card.Text>
                                {project.projectUrl? 
                                    (<Button variant="primary">
                                        <a href={project.projectUrl} target="_blank" rel="noreferrer">
                                            View Project
                                        </a>
                                    </Button>)
                                    : null
                                }           
                            </Card.Body>
                        </Card>
                    </Col> 
                    );
                })}             
            </Row>
        </Container>
        </Element>
    )
}
export default withNamespaces()(Project);